import React, { Component } from "react";
import Media from "react-media";
import ContactCard from "./../components/ContactCard";
import { withTranslation } from "react-i18next";

const paperPc = {
  maxWidth: "30%",
};

const paperMobile = {
  maxWidth: "80%",
};

const data = [
  {
    title: "gmail",
    content: "descripcion_email",
    src: require("./../images/gmail.png"),
    href: "enlace_email",
  },
  {
    title: "linkedin",
    content: "descripcion_linkedin",
    src: require("./../images/linkedin.png"),
    href: "enlace_linkedin",
  },
];

class ContactScreen extends Component {
  constructor(props) {
    super(props);

    this.state = {};
  }

  componentDidMount() {
    document.title = "MBM - Contact";
  }

  render() {
    return (
      <div className="Home ScreenContainer">
        {data.map((item) => (
          <Media query="(max-width: 1025px)" key={item.title}>
            {(matches) =>
              matches ? (
                <ContactCard
                  title={item.title}
                  src={item.src}
                  content={this.props.t(item.content)}
                  href={this.props.t(item.href)}
                  paperStyle={paperMobile}
                />
              ) : (
                <ContactCard
                  title={item.title}
                  src={item.src}
                  content={this.props.t(item.content)}
                  href={this.props.t(item.href)}
                  paperStyle={paperPc}
                />
              )
            }
          </Media>
        ))}

        <br />
      </div>
    );
  }
}

export default withTranslation("common")(ContactScreen);
